.scontainer {
  margin: 0 auto;
  max-width: 1200px;
  padding: 10px;
  width: 100%;
}
.scontainer.scontainer--half {
  display: inline-block;
  max-width: 600px;
}
.scontainer.scontainer--full {
  padding-bottom: 0px;
  padding-left: 0px;
  padding-right: 0px;
  padding-top: 0px;
}
.scontainer.scontainer--relative {
  position: relative;
}
.line {
  font-size: 0px;
  letter-spacing: 0px;
  margin: 0 auto;
  padding: 0px;
  word-spacing: 0px;
}
.line > * {
  font-size: 16px;
  letter-spacing: initial;
  word-spacing: initial;
}
[class*=":scol"]:not([class*='scol--full']) .line {
  margin: -10px;
}
.scol-break {
  width: 100%;
}
[class*=":scol"] {
  display: inline-block;
  padding: 10px;
  vertical-align: middle;
  width: 100%;
}
[class*=":scol"].scol--relative {
  position: relative;
}
[class*=":scol"].scol--full {
  padding: 0px;
}
